@import "../Abstract/__variables";
@import "../Abstract/__extend";
@import "../Abstract/__mixin";

.topTittle {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    img {
        // width: 100%
        height: 25vw;
        display: block;
        min-height: 350px;
        @include m() {
            width: auto;
            min-height: 300px; } }
    &-h2 {
        line-height: 8rem;
        position: absolute !important;
        text-align: center;
        color: white;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        max-width: 340px;
        width: 100%;
        @extend %_f36Mincho;
        pointer-events: none;
        @include m() {
            line-height: 6rem;
            &::after, &::before {
                left: 20px;
                right: 20px; } } } }
.people {
    @include commonPeople;
    &-list {
        margin-top: 100px;
        padding-bottom: 40px; }
    &-per {
        margin-bottom: 60px; } }
@import "../Layout/__common";
