
.shapeSkew {
    display: block;
    position: relative;
    z-index: 0;
    text-align: center;
    color: white;
    &::after {
        @include psuedoDefault(0px);
        background-color: $colorDefault;
        transform: skewX(-20deg);
        z-index: -1; } }
.link-parent {
    position: relative;
    .link-a {
        position: absolute;
        top: 0px;
        left: 0px;
        width: 100%;
        height: 100%;
        z-index: 1; } }
